.regionsWrapper {
  position: relative;
}

.regions {
  position: absolute;
  top: 85px;
  left: 6px;
  max-height: 200px;
  z-index: 2;
  list-style-type: none;
  border: 1px solid #4e5994;
  overflow-y: scroll;
  width: calc(100% - 20px);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.region {
  font-size: 19px;
  padding: 13px 15% 13px 13px;
  cursor: pointer;
}

.region:hover {
  background: #4e5994;
  color: white;
}